*{
    margin: 0;
    padding: 0;
}
.header-container{
    box-shadow: 0px 0px 20px black;
}
.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 5%;
    height: 15vh;
}
.logo{
    display: flex;
    justify-content: center;
    align-items: center;
}
.logo>img{
    height: 15vh;
    border-radius: 50%;
}
.nav-items>ul{
    display: flex;
    list-style-type: none;
    column-gap: 50px;
}
.nav-items>ul>a>li>i{
    padding-right: 0.5rem;
}
.nav-items>ul>a{
    text-decoration: none;
    color: black;
}
